*{
  box-sizing: border-box;
}
button{
  outline:  none;
  border : 0;
  cursor: pointer;
}

.habit{
  font-size: 2.5rem;
  padding : 0.5em;
  list-style:  none;
}

.habit-button{
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}

.btn-add{
  font-size: 1.4rem;
  margin: 0 0.1em;
  background-color: green;
  color : white;
  list-style:  none;
  border: none;
}

.navbar-count{
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.input-habit{
  font-size: 1.4rem;
  margin: 0 0.1em;
  list-style:  none;
  border : 1px solid blue;
}

.habit-navbar{
  font-size: 2.5rem;
  padding: 1em;
  background-color: #F9DE7D;
}

.btn-reset{
  
  font-size: 1.5rem;
  padding: 10px;
  margin: 0 0.1em;
  color : white;
  background-color: green;
  border: none;
}



.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease{
  color: goldenrod;
}

.habit-delete{
  font-size : 2rem;
  color:darkred;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

